import React from 'react'
import { ListItem } from './ListItem.js'

export const List = ({ birthdays, onDelete, onReminder }) => {
    return (
        <div className="chikkin">
            {birthdays.map((birthday) => (
                <ListItem onDoubleClick={() => onReminder(birthday.id)} key={birthday.id} birthday={birthday} onDelete={onDelete}/>
            ))}
        </div>
    )
}
