import React from 'react'
import { FaTimes } from 'react-icons/fa';
import './App.css'

export const ListItem = ({ birthday, onDelete }) => {
    // eslint-disable-next-line
    const { id, name, date, reminder } = birthday;
    return (
        <div>
            <article className={`person ${reminder ? 'reminded' : ''}`}>
                <h1>
                    {name} <FaTimes style={{ color: 'red' }} onClick={() => onDelete(id)}/>
                </h1>
                <h3>
                    {date}
                </h3>
            </article>
        </div>
    )
}
