import React from 'react'
import { useState } from 'react'

export const AddBirthday = ({ onAdd, isReminder }) => {

    const [name, setName] = useState('');
    const [date, setDate] = useState('');
    const [reminder, setReminder] = useState(false)
    
    const onSubmit = (e) => {
        e.preventDefault()
        if (!name || !date) {
            alert('Please fill in all Fields');
            return;
        }

        onAdd({ name, date, reminder })

        setName('');
        setDate('');
        setReminder(false)

    }

    return (
        <div>
            <form onSubmit={onSubmit}>
                <div>
                    <label>Add Person</label>
                    <input type="text" value={name} placeholder="Add Person" onChange={(e) => setName(e.target.value)} />
                </div>
                <div>
                    <label>Add Date</label>
                    <input type="text" value={date} placeholder="Add Date" onChange={(e) => setDate(e.target.value)} />
                </div>
                <div>
                    <label>Reminder</label>
                    <input type="checkbox" checked={reminder} onChange={(e) => setReminder(e.currentTarget.checked)} />
                </div>

                <input type="submit" value="Add A Birthday" />
            </form>
        </div>
    )
}
