import { List } from './List.js'
import { useState } from 'react';
import { AddBirthday } from './AddBirthday.js';
import './App.css';

function App() {
  // eslint-disable-next-line
  const [birthdays, setBirthdays] = useState([
    {
      id: 1,
      name: 'Snope',
      date: 'date',
      reminder: true
    },
    {
      id: 2,
      name: 'Tep',
      date: 'sdfsdf',
      reminder: false
    }
  ])

  const gapi = window.gapi;
  var CLIENT_ID = '138750241048-qnep6jnrv8nj93ptqrb5u6fju6c7ojn1.apps.googleusercontent.com';
  var SCOPES = "https://www.googleapis.com/auth/calendar.readonly";
  var API_KEY = 'AIzaSyCSURxHNRaURLqkDBDM1HGgCOjsKRFDmA0';
  var DISCOVERY_DOCS = "https://www.googleapis.com/discovery/v1/apis/calendar/v3/rest";

  const setCalendar = () => {
    gapi.load('client:auth2', () => {
      console.log('Loaded Client');

      gapi.client.init({
        apiKey: API_KEY,
        clientId: CLIENT_ID,
        discoveryDocs: DISCOVERY_DOCS,
        scope: SCOPES
      });

      gapi.client.load('calendar', 'v3', () => console.log('Bam!'));

      gapi.auth2.getAuthInstance().signIn()


    })
  }

  const appendBirthday = (param) => {
    // eslint-disable-next-line
    const { name, date, reminder } = param;
    if (reminder === true) {
      setCalendar()
    }
    const id = Math.random(Math.floor() * 10000) + 1
    const titleChange = { ...param, name: `${param.name}!!`}
    const newBirth = { id, ...titleChange }
    setBirthdays([ ...birthdays, newBirth ])
  }

  const deleteBirthday = (id) => {
    setBirthdays(birthdays.filter((birthdays) => birthdays.id !== id));
  }

  const setReminder = (id) => {
    birthdays.map((birthday) => birthday.id === id ? { ...birthday, reminder: !birthday.reminder } : birthday)
  }

  const birthdayCount = birthdays.length;

  return (
    <div className="App">
      <section className='container'>
        <h2>{birthdayCount} Birthdays</h2>
        <AddBirthday onAdd={appendBirthday}/>
        <List birthdays={birthdays} onDelete={deleteBirthday} onReminder={setReminder}/>
      </section>
    </div>
  );
}

export default App;
